.landing-page {
  // $sans: "Open sans";
  a {
    color: #10a3ff;
    &:hover {
      color: #10a3ff;
    }
  }
  font-family: $regular-helvetica;
  overflow-x: hidden;
  .mobile_ {
    @media screen and (max-width: 600px) {
      text-align: center !important;
    }
  }
  a {
    text-decoration: none;
  }
  .landing-hero {
    background-color: #0f1e41;

    .nav-wrapper {
      padding-top: 2em;
      background-color: #0f1e41;
      height: 100px;
      a {
        padding-left: 0px !important;
        @media screen and (max-width: 600px) {
          max-width: 100px;
          svg {
            width: 135%;
          }
        }
      }

      .wrap-btn {
        float: right;
        height: 50px;
        padding: 15px 0px 15px 15px;
        font-size: 18px;
        line-height: 20px;
        button {
          background-color: #10a3ff;
          color: white;
          border-radius: 25px;
          padding: 15px 25px;
          outline: none;
          border: transparent;
          @media screen and (max-width: 600px) {
            padding: 9px 15px;
            margin-top: -9px;
            font-size: 16px;
          }
          &:focus {
            outline: none;
          }
          a {
            text-decoration: none;
            color: white;
          }
        }
      }
    }
    .hero-section {
      padding: 5em 0;

      @media screen and (max-width: 600px) {
        padding-bottom: 10em;
      }
      .hero-caption {
        @media screen and (max-width: 600px) {
          text-align: center;
        }
        h1 {
          text-align: left;
          margin-top: 0px;
          font-family: $helvetica-bold;
          color: #10a3ff;

          font-size: 55px;
          @media screen and (min-width: 601px) {
            font-size: 32px;
          }
          @media screen and (min-width: 1200px) {
            font-size: 50px;
          }
          @media screen and (max-width: 600px) {
            text-align: center;
            font-size: 45px;
          }

          @media screen and (min-width: 1660px) {
            font-size: 70px;
          }
        }
        .caption-subtitle {
          max-width: 80%;
          @media screen and (min-width: 1660px) {
            width: 80%;
          }
          @media screen and (max-width: 600px) {
            max-width: 100%;
          }
          span {
            color: white;
            line-height: 2;
            font-family: $regular-helvetica;
            font-size: 16px;

            @media screen and (min-width: 1250px) {
              font-size: 17px;
            }
            @media screen and (min-width: 1560px) {
              font-size: 20px;
            }
            @media screen and (max-width: 960px) and (min-width: 601px) {
              font-size: 13px;
              line-height: 1.3;
            }
          }
        }
      }
      .coming-soon {
        margin-top: 4em;
        color: #10a3ff;
        margin-top: 4em;
        color: #10a3ff;
        position: absolute;
        bottom: 7%;
        @media screen and (max-width: 600px) {
          position: relative;
          bottom: 0;
        }
        img {
          padding-right: 1em;
        }
      }
      .hero-image {
        img {
          right: 45%;
          position: relative;
          width: 150%;
          bottom: 20px;
          @media screen and (max-width: 600px) {
            position: relative;
            width: 100%;
            top: 30px;
            right: 0;
          }
          @media screen and (min-width: 1660px) {
            width: 150%;
            right: 55%;
          }
        }
      }
    }
  }
  .hero-image-mobile {
    font-family: "Open Sans" !important;
    img {
      position: relative;
      bottom: 140px;
      width: 100%;
    }
  }

  .trusted-section {
    .trusted-wrapper {
      margin-top: 3em;
      margin-bottom: 3em;
      padding-top: 2em;

      .heading_caption {
        margin: 0 auto;
        text-align: center;
        width: 75%;
        h2 {
          color: #0f1e41;
          font-family: $helvetica-bold;
        }
        span {
          padding-top: 2em;
          display: block;
          font-family: "Open sans" !important;
          font-size: 17px;
          color: #0f1e41;

          @media screen and (min-width: 1600px) {
            font-size: 20px;
          }
        }
      }
      .school-animated {
        position: relative;
        img.school {
          width: 75%;
        }
        .one {
          width: 5%;
          position: absolute;
          left: 10%;
          top: 10%;
        }
        .two {
          width: 5%;
          position: absolute;
          left: 0%;
          top: 20%;
        }
        .three {
          width: 5%;
          position: absolute;
          bottom: 55%;
        }
        .four {
          width: 5%;
          bottom: 35%;
          position: absolute;
          left: 0%;
        }
        .five {
          width: 5%;
          position: absolute;
          right: 20%;
        }
        .five {
          width: 5%;
          position: absolute;
          /* right: 16%; */
          top: 10%;
        }
        .six {
          width: 5%;
          position: absolute;
          right: 12%;
          top: 28%;
        }
        .seven {
          width: 5%;
          position: absolute;
          right: 3%;
          bottom: 55%;
        }
        .eight {
          width: 5%;
          position: absolute;
          right: 0%;
          bottom: 30%;
        }

        &:hover {
          @keyframes straightLine {
            50% {
              transform: translate3D(100px, 100px, 0);
              animation-timing-function: cubic-bezier(0.3, 0.27, 0.07, 1.64);
            }
          }
          @keyframes straightLine {
            50% {
              transform: translate3D(100px, 100px, 0);
              animation-timing-function: cubic-bezier(0.02, 0.01, 0.21, 1);
            }
          }
          @keyframes secondLine {
            50% {
              transform: translate3D(-200px, 100px, 0);
            }
          }

          .one {
            width: 5%;
            //animation: straightLine 4.5s infinite linear;
            animation: straightLine 4.5s infinite
              cubic-bezier(0.02, 0.01, 0.21, 1);
            animation-direction: alternate;
          }
          .two {
            width: 5%;
            animation: straightLine 4.5s infinite
              cubic-bezier(0.02, 0.01, 0.21, 1);
            animation-delay: 2s;
            animation-direction: alternate-reverse;
          }
          .three {
            width: 5%;
            animation: straightLine 4.5s infinite linear;
          }
          .four {
            width: 5%;
            animation: straightLine 4.5s infinite
              cubic-bezier(0.02, 0.01, 0.21, 1);
            animation-direction: alternate;
            animation-delay: 5s;
          }
          .five {
            width: 5%;
            animation: secondLine 4.5s infinite linear;
            animation-direction: reverse;
          }
          .six {
            width: 5%;
            animation: straightLine 4.5s infinite
              cubic-bezier(0.02, 0.01, 0.21, 1);
            animation-delay: 1s;
            animation-direction: alternate-reverse;
          }
          .seven {
            width: 5%;
            animation: secondLine 5.5s infinite linear;
            animation-direction: reverse;
          }
          .eight {
            width: 5%;
            animation: secondLine 6.5s infinite linear;
          }
        }
      }
    }
    img {
      width: 100%;
    }
  }
  .featured-section {
    font-family: "Open Sans" !important;
    padding-bottom: 3em;
    padding-left: 2em;
    padding-right: 2em;

    @media screen and (max-width: 768px) {
      padding-left: 0;
      padding-right: 0;
    }
    @media screen and (min-width: 1400px) {
      padding-left: 5em;
      padding-right: 5em;
    }
    @media screen and (min-width: 1600px) {
      padding-left: 7em;
      padding-right: 7em;
    }
    margin-top: -5em;
    .header {
      text-align: center;
      padding-bottom: 1em;

      h2 {
        color: #002984;
        font-family: $helvetica-bold;
      }
      span {
        color: #10a3ff;
        font-size: 16px;
        padding-top: 1em;
        display: block;
        padding-bottom: 2em;
        font-family: "Open Sans" !important;
        @media screen and (min-width: 1600px) {
          font-size: 20px;
        }
      }
    }
    // .trapezoid {
    //   border-bottom: 280px solid red;
    //   border-left: 25px solid transparent;
    //   border-right: 0px solid transparent;
    //   height: 0;

    //   // background: #ffffff;
    //   box-shadow: 0px 18px 35px rgba(0, 0, 0, 0.05);
    // }
    .magnet {
      width: 20%;
      position: absolute;
      top: -10%;
      right: -20%;
    }
    .search {
      width: 20%;
      position: absolute;
      top: -10%;
      left: -20%;
    }
    .earth {
      width: 20%;
      position: absolute;
      bottom: 10%;
      left: -20%;
    }
    .trapezoid {
      img {
        width: 90%;
      }
    }
    .trapezoid-text {
      padding-top: 15%;
      @media screen and (max-width: 600px) {
        padding-top: 3%;
      }
      text-align: left;
      h2 {
        color: #002984;
        font-family: $helvetica-bold;
      }
      .text {
        max-width: 100%;
        color: #002984;
        line-height: 1.6;
        font-size: 15px;
        font-family: "Open Sans" !important;
        @media screen and (min-width: 1200px) {
          font-size: 15px;
          line-height: 1.6;
        }
        @media screen and (min-width: 1400px) {
          font-size: 17px;
          line-height: 1.6;
        }
        @media screen and (min-width: 1600px) {
          font-size: 20px;
        }
        @media screen and (max-width: 600px) {
          max-width: 100%;
        }
      }

      .learn-more {
        text-align: left;

        button {
          margin-top: 0.6em;
          background-color: transparent;
          border: none;
          outline: none;
          font-size: 16px;
          color: #10a3ff;
          font-family: $italic-sans;
          padding-left: 0px !important;
          @media screen and (min-width: 1300px) {
            margin-top: 1.4em;
          }
          @media screen and (min-width: 1600px) {
            margin-top: 2em;
          }
          @media screen and (max-width: 600px) {
            margin-top: 1em;
          }

          img {
            width: 20%;
            padding-left: 5px;
          }
        }
      }
    }
  }

  .newsletter {
    background-color: #0082d3;
    max-height: 330px;
    padding-left: 2em;
    font-family: "Open Sans" !important;
    padding-right: 2em;

    @media screen and (max-width: 768px) {
      padding-left: 0;
      padding-right: 0;
    }
    @media screen and (min-width: 1400px) {
      padding-left: 5em;
      padding-right: 5em;
    }
    @media screen and (min-width: 1600px) {
      padding-left: 7em;
      padding-right: 7em;
    }
    @media screen and (max-width: 768px) {
      max-height: fit-content;
    }
    img {
      position: relative;
      bottom: 20%;

      @media screen and (max-width: 768px) {
        position: relative;
        bottom: 0;
        top: 18%;
        right: 7%;
      }
      @media screen and (min-width: 1920px) {
        left: 7%;
      }
    }
    .col-md-7 {
      padding-top: 3em;
      h5 {
        color: white;
        font-family: $sans;
        font-weight: normal;
      }
      h2 {
        font-family: $helvetica-bold;
        color: white;
      }
      form {
        max-width: 500px;
        margin: 0;
        padding: 2em 0;
        .row {
          border-radius: 25px;

          input {
            border: none;
            outline: none;
            padding: 0.9em 3em;
            width: 100%;
            min-height: 45px;
          }
          button {
            width: 100%;
            padding: 0.9em;
            border: none;
            border-radius: 25px;
            position: relative;
            right: 18%;
            color: white;
            background-color: #00428b;
          }
        }
      }
    }
  }

  .pre-footer {
    border-top: 1px solid #10a3ff;
    padding-top: 3em;
    span {
      a {
        color: #212353;
        text-decoration: none;
        &:hover {
          text-decoration: none;
        }
      }
    }
    .footer1 {
      padding-bottom: 2em;
      h2 {
        color: #002984;
        font-family: $helvetica-bold;
        @media screen and (max-width: 600px) {
          text-align: center;
        }
        @media screen and (min-width: 1400px) {
          font-size: 42px;
        }
      }
      h6 {
        color: #10a3ff;
        @media screen and (max-width: 600px) {
          text-align: center;
        }
        @media screen and (min-width: 1400px) {
          font-size: 20px;
        }
      }
      .button-wrappa {
        margin-top: 20px;
        display: flex;
        justify-content: space-between;
        @media screen and (max-width: 600px) {
          justify-content: space-around;
        }
        button.learn-more {
          background-color: #10a3ff;
          color: white;
          border-radius: 20px;
          font-family: $italic-sans;
          padding: 10px 30px;
          outline: none;
          border: transparent;
          &:focus {
            outline: none;
          }
          @media screen and (max-width: 768px) {
            border-radius: 20px;
            padding: 10px 20px;
          }

          @media screen and (min-width: 1600px) {
            border-radius: 25px;
            padding: 15px 40px;
          }
        }
        button.request-demo {
          background-color: white;
          color: #10a3ff;
          border-radius: 20px;
          padding: 10px 30px;
          font-family: $italic-sans;
          outline: none;
          box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
          border: transparent;
          @media screen and (max-width: 768px) {
            border-radius: 20px;
            padding: 10px 20px;
          }
          @media screen and (min-width: 1600px) {
            border-radius: 25px;
            padding: 15px 40px;
          }
          &:focus {
            outline: none;
          }
          img {
            padding-left: 1em;
          }
        }
      }
    }
    .footer2 {
      @media screen and (min-width: 1400px) {
        font-size: 20px;
      }
      .col-md-6 {
        padding-bottom: 1em;
        img {
          width: 40%;
        }
      }
      .col-xs---6 {
        @media screen and (max-width: 600px) {
          width: 49%;
          padding-left: 15%;
        }
      }
      .level-one {
        display: block;
        padding-bottom: 2em;
        text-align: left;
      }
      .level-two {
        display: block;
        padding-top: 1em;
        text-align: left;
        img {
          width: 25%;
          padding-right: 2%;
          @media screen and (max-width: 600px) {
            width: 20%;
          }
        }
        // @media screen and (max-width: 600px) {
        //   text-align: center;
        // }
      }
    }

    .footer3 {
      .level-one {
        display: block;
        text-align: left;
        padding-bottom: 2em;
      }
      .level-two {
        display: block;
        text-align: left;
        @media screen and (max-width: 600px) {
          text-align: center;
          img {
            width: 20%;
          }
        }
      }
    }
    .footer4 {
      .level-one {
        display: block;
        padding-bottom: 2em;
      }
      .level-two {
        display: block;
        img {
          width: 50%;
        }
      }
    }
  }
  .bottom-footer {
    padding-top: 1em;
    padding-bottom: 1em;
    @media screen and (max-width: 768px) {
      text-align: center !important;
      padding: 2em 0;
      img {
        padding-top: 0.5em;
        width: 45%;
        margin: 0px auto;
      }
    }
    span {
      display: block;
      color: #212353 !important;
      a {
        color: #212353 !important;
      }
    }
    .col-md-10 {
      span {
        bottom: 0;
        position: absolute;
      }
    }
  }

  .container___ {
    margin-left: 15px;
    .grid-container {
      margin-right: -15px;
    }
    @media screen and (min-width: 800px) {
      margin-left: 14%;
    }
    @media screen and (min-width: 1024px) {
      margin-left: 9%;
    }
    @media screen and (min-width: 1200px) and (max-width: 1250px) {
      margin-left: 7.5%;
    }
    @media screen and (min-width: 1250px) and (max-width: 1300px) {
      margin-left: 7.75%;
    }
    @media screen and (min-width: 1300px) and (max-width: 1350px) {
      margin-left: 6.5%;
    }
    @media screen and (min-width: 1350px) and (max-width: 1400px) {
      margin-left: 8.5%;
    }
    @media screen and (min-width: 1400px) and (max-width: 1450px) {
      margin-left: 10.5%;
    }
    @media screen and (min-width: 1450px) and (max-width: 1500px) {
      margin-left: 7.5%;
    }
    @media screen and (min-width: 1500px) and (max-width: 1550px) {
      margin-left: 8%;
    }
    @media screen and (min-width: 1550px) and (max-width: 1600px) {
      margin-left: 9.25%;
    }
    @media screen and (min-width: 1600px) and (max-width: 1650px) {
      margin-left: 9.5%;
    }
    @media screen and (min-width: 1650px) and (max-width: 1685px) {
      margin-left: 9%;
    }
    @media screen and (min-width: 1685px) and (max-width: 1695px) {
      margin-left: 12%;
    }
    @media screen and (min-width: 1700px) and (max-width: 1750px) {
      margin-left: 9%;
    }
    @media screen and (min-width: 1800px) and (max-width: 1850px) {
      margin-left: 9%;
    }
    @media screen and (min-width: 1900px) and (max-width: 1950px) {
      margin-left: 11%;
    }

    @media screen and (min-width: 2000px) {
      margin-left: 14%;
    }
  }

  .cancel-bottom-140 {
    @media screen and (max-width: 600px) {
      margin-top: -140px;
    }
  }

  .d__none {
    display: none !important;

    @media screen and (max-width: 600px) {
      display: block !important;
    }
  }

  .d--none {
    display: none !important;

    @media screen and (min-width: 601px) {
      display: block !important;
    }
  }
}
