.thanks-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 85vh;
  background-image: url("/images/thankyoubg.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  min-height: 100%;

  img {
    justify-content: center;
    align-items: center;
    left: 20%;
    position: absolute;
    width: 15%;
    @media screen and (min-width: 1400px) {
      left: 22%;
      width: 17%;
    }

    @media screen and (max-width: 768px) {
      left: 0%;
      width: 45%;
    }
  }

  .thanks-section {
    margin-top: -5em;
    width: 40em;
    padding-bottom: 5em;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    h2 {
      padding-top: 2em;
    }

    @media screen and (min-width: 1500px) {
      width: 52em;
    }

    @media screen and (max-width: 500px) {
      margin-top: -5em;
      padding-bottom: 9em;
      width: 22em;

      h2 {
        font-size: 20px;
        padding-top: 2em;
      }
    }

    .header {
      font-size: 25px;
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
      background-color: #10a3ff;
      padding: 0.8em;
      color: white;
      font-family: $helvetica-bold;

      @media screen and (max-width: 600px) {
        font-size: 18px;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        background-color: #10a3ff;
        padding: 0.5em;
        color: white;
        font-family: "Helvetica Bold";
      }
    }
    .thanks-content {
      .caption {
        font-size: 18px;
        font-family: $regular-helvetica;
        line-height: 1.8;
        padding: 2em 0 1em 0;

        span {
          color: #10a3ff;
          font-family: $helvetica-bold;
          display: inline-block;
          padding-bottom: 1em;
        }

        @media screen and (max-width: 600px) {
          font-size: 15px;
          font-family: "Helvetica Regular";
          line-height: 1.4;
          padding: 1em 4em;
        }
      }
    }
  }
}
